<template>
  <div class="download full-screen">
    <pem-scroll-view ref="scroll" class="scroll-view">
      <div class="download-wrapper" v-if="!isWeixin">
        <div class="logo"></div>
        <div class="txt-title">项目工场</div>
        <div class="txt-content">找项目、找投资人信息，就用项目工场</div>
        <div class="download-btn" @click="openNow">立即下载</div>
      </div>

      <div class="guide-wrapper" v-if="isWeixin">
        <div class="guide-inner">
          <p>1、点击右上角 “…”</p>
          <p v-if="isIOS">2、选择在“safari”打开</p>
          <p v-if="!isIOS">2、选择在“浏览器”打开</p>
          <p>3、打开APP</p>
        </div>
      </div>
    </pem-scroll-view>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import { setTitle, device } from 'common/js/util'
import { MiniSiteApi } from 'api'

export default {
  components: {
    PemScrollView
  },
  data () {
    return {
      isIOS: device.isIOS(),
      isWeixin: device.isWeixin(),
      isAndroid: device.isAndroid(),
      type: '', // -2: APP推广
      id: '',
      androidSchemeUrl: '',
      apkUrl: ''
    }
  },
  created () {
    this.initPage()
  },
  methods: {
    initPage () {
      setTitle('投融资信息对接服务平台')
      this.wxShare({})
      this.type = this.$route.query.type || -1
      this.id = this.$route.query.id || -1
      this.androidSchemeUrl = `pemarket://www.pemarket.com.cn/attr?type=${this.type}&id=${this.id}`
      if (!this.isWeixin && this.isAndroid) {
        window.location.href = this.androidSchemeUrl
        if (+this.type === -2) {
          window.location.href = `http://cdn.pemarket.com.cn/apptg/${this.id}.apk`
        }
        this.getAndroidApkUrl()
      }
    },
    openNow () {
      if (this.isIOS) {
        window.location.href = 'https://itunes.apple.com/cn/app/id1057083949?mt=8'
      } else {
        window.location.href = this.androidSchemeUrl
        setTimeout(() => {
          if (+this.type === -2) {
            window.location.href = `http://cdn.pemarket.com.cn/apptg/${this.id}.apk`
          } else {
            window.location.href = this.apkUrl
          }
        }, 2000)
      }
    },
    getAndroidApkUrl () {
      MiniSiteApi.getApkUrl().then(data => {
        this.apkUrl = (data && data.url) ? data.url : ''
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/mixin"
  .download-wrapper
    background: #ffffff
    text-align: center
    .logo
      width: 5.28rem
      height: 3.8rem
      margin: 1rem auto 0.4rem
      bg-image('./logo')
      background-size: 100% 100%
      background-repeat: no-repeat
    .txt-title
      font-family: HYe3gj
      font-size: 0.5rem
      font-weight: bold
      line-height: 0.5rem
      color: #3F3F3F
    .txt-content
      font-family: PingFangSC-Light
      font-size: 0.32rem
      line-height: 0.44rem 
      color: #202020
      margin-top: 0.48rem
    .download-btn
      font-family: PingFangSC-Medium
      font-size: 0.32rem
      color: #41403E
      height: 1.36rem
      line-height: 0.8rem
      margin-top: 2.06rem
      bg-image('./bg_download')
      background-size: 3.42rem 1.36rem
      background-repeat: no-repeat
      background-position: center
  .guide-wrapper
    background: #ffffff
    padding-top: 0.38rem
    .guide-inner
      box-sizing: content-box
      width: 6.98rem
      min-height: 14.32rem
      padding-top: 0.2rem
      bg-image('./bg_guide')
      background-size: 7.92rem 14.32rem
      background-repeat: no-repeat
      background-position: right top
      p 
        font-family: PingFangSC-Medium
        font-size: 0.36rem
        font-weight: bold
        color: #333333
        line-height: 0.5rem 
        margin-top: 0.4rem
        padding-left: 1.48rem
</style>



